<template>
  <div class="addressadd">
    <!-- head部分 begin -->
    <!-- <div class="nav-bar">
      <div class="nav-bar-left">
        <svg aria-hidden="true" class="icon" @click="goBack()">
          <use xlink:href="#iconicon-back" />
        </svg>
      </div>
      <div class="nav-bar-title">新增发票地址</div>
    </div> -->
    <headerBar :header-data="headerData" />
    <!-- head部分 end -->
    <div class="container">

      <!-- 新增发票地址 begin -->
      <div class="content">
        <div class="common-main box">

          <div class="content common-main">
            <van-field v-model="header" label="收货人姓名" label-width="110px" required placeholder="请输入收货人姓名" />
            <van-field v-model="company_code" label="手机号码" label-width="110px" required placeholder="请输入手机号码" />
            <van-field v-model="company_address" label="固定电话" label-width="110px" required placeholder="请输入固定电话" />
            <div class="spe-list">
              <div class="spe-left"><span class="tit">默认地址</span></div>
              <div class="spe-right">
                <van-switch v-model="checked" size="20px" />
              </div>
            </div>
            <div @click="showPopup">
              <van-field v-model="address" label="收货地址" label-width="110px" required placeholder="请选择地址" />
            </div>
          </div>
          <div class="bt-group">
            <van-button type="info" size="small" @click="saveInvoice()">保存</van-button>
          </div>

        </div>
      </div>

      <!-- 新增发票地址 end -->
    </div>
    <!-- 地址弹出层 beign -->
    <van-popup
      v-model="show"
      position="bottom"
      :style="{ height: '70%' }"
    >
      <!-- <van-area ref="myArea" :area-list="areaList" :value="addressShow" :columns-num="3" @change="onChange" @confirm="onConfirm" @cancel="onCancel" /> -->
      <XyAddress @getCheckResult="getCheckResult" />
    </van-popup>
    <!-- 地址弹出层 end -->
  </div>
</template>

<script land="script" scoped>

import headerBar from '@/components/header/headerBar.vue'
import XyAddress from '@/components/address/address.vue'
// import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'InvoiceAddressAdd',
  components: {
    headerBar,
    XyAddress
  },
  // mixins: [pageRouter],
  data() {
    return {
      addressShow: '', // 地区地址code码
      address: '', // 地区地址
      invoice: '', // 发票信息
      header: '', // 发票抬头
      company_code: '', // 纳税人识别码
      company_address: '', // 注册地址
      company_phone: '', // 注册电话
      company_bank: '', // 开户银行
      company_bankname: '', // 银行账户
      szjd: '', // 所在街道
      invoiceprovice: '', // 省份
      invoicecity: '', // 市
      invoicecounty: '', // 县
      invoicetown: '', // 区/镇
      invoiceaddress: '', // 详细地址
      checked: true,
      show: false,
      radio: '1',
      addressmodel: '',
      id: '',
      invoicecontent: '', // 发票内容
      fptt: '',
      nsrsbm: '',
      fpnr: '',
      headerData: {
        title: '新增发票地址',
        leftUrl: 'back',
        leftText: ' '
      }
    }
  },
  created() {

  },
  methods: {
    // 获取地址组件返回值
    getCheckResult(sheng, shi, xian, xiang, state) {
      if (!state) {
        this.show = false
      }
      if (sheng) {
        this.address = sheng.text
        this.invoiceprovice = sheng.value
      }
      if (shi) {
        this.address = this.address + '/' + shi.text
        this.invoicecity = shi.value
      }
      if (xian) {
        this.address = this.address + '/' + xian.text
        this.invoicecounty = xian.value
      }
      if (xiang) {
        this.address = this.address + '/' + xiang.text
        this.invoicetown = xiang.value
      }
    },
    saveInvoice() {
      this.$router.back()
    },
    showPopup() {
      this.show = true
    },
    // value=0改变省，1改变市，2改变区
    onChange(picker, index, value) {
      const val = picker.getValues()
      console.log(val)// 查看打印
      let areaName = ''
      for (var i = 0; i < val.length; i++) {
        areaName = areaName + (i === 0 ? '' : '/') + val[i].name
      }
      this.addressmodel = areaName
    },
    // 确定选择城市
    onConfirm(val) {
      console.log('val-----', val)
      this.invoiceprovice = val[0].name
      this.invoicecity = val[1].name
      this.invoicecounty = val[2].name
      this.address = val[0].name + '/' + val[1].name + '/' + val[2].name
      this.addressShow = val[2].code
      this.show = false// 关闭弹框
    },
    // 取消选中城市
    onCancel() {
      this.show = false
      this.$refs.myArea.reset()// 重置城市列表
    }
  }
}
</script>

<style lang="css" scoped>

</style>
